import { Injectable } from '@angular/core';
import { Planta } from '../planta';
import { PLANTAS } from './mock-plantas';
import { AngularFirestore } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class DataService {
  plantas: Observable<any[]>;

  constructor(private db: AngularFirestore) { }

  getPlanta(id: number) {
    return this.db.collection('plantas').doc<Planta>('PlqwVEfCeFWRFrQRcQap').valueChanges();
  };

  searchPlanta(nombre: string) {
    return this.db.collection('plantas', ref => ref.where('nombre', '==', nombre)).valueChanges();
  }

}



